* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  height: 60px;
  width: 100%;
  background-color: #0ff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 768px;
  justify-content: center;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

.search-form {
  width: 254px;
  height: 30px;
  background-color: #fff9f9;
  border: 1px solid #7eb355;
  border-radius: 5px;
  padding: 3px 12px 4px 24px;
  display: flex;
}

.search-form:hover {
  border-color: #1d00ff;
}

input {
  color: #5a441d;
  width: 192px;
  background-color: #fff;
  border: none;
  outline: none;
  padding: 0;
  font-size: 18px;
}

button {
  color: #4f432e;
  cursor: pointer;
  background-color: #fbfaff;
  border: none;
  padding: 0;
  font-size: 18px;
  line-height: 20px;
}

button.hidden {
  display: none;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -10px;
  padding-top: 60px;
  display: flex;
}

.gallery__item {
  min-width: 320px;
  border: 2px solid #f1eeee;
  border-radius: 5px;
  flex-basis: calc(20% - 2px);
  margin-top: 10px;
  margin-left: 10px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery-img {
  height: 30vh;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: column;
  flex-flow: column;
  padding: 10px;
  display: flex;
}

.info-item {
  margin: 0;
}

